.welcome-hero {
  width: 90%;
  margin-top: 50rem;
  z-index: 100;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-end;
}
.welcome-hero h1 {
  pointer-events: none;
  font-size: 8rem;
  color: #ffecb3;
  -webkit-text-stroke: 1px #ffecb3;
  text-align: center;
  width: 100%;
  margin-block-end: 0;
  font-weight: 1000;
  letter-spacing: 2px;
  margin-top: -25rem;
  width: 70%;
}
.welcome-hero span {
  color: #ffecb3;
}
.welcome-hero p {
  margin-bottom: 2rem;
  width: 60%;
  text-align: center;
  white-space: normal;
  font-size: 1.7rem;
  color: #ffecb3;
  font-weight: 200;
  padding: 1rem;
  pointer-events: none;
}
.welcome-hero-button {
  margin-top: 3rem;
  background: transparent;
  border: none;
  color: #ffecb3;
  font-weight: 100;
  outline: none;
  font-size: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0.9;
  position: fixed;
  top: 88vh;
}
.welcome-hero-button h1 {
  margin-top: 1rem;
  font-size: 1rem !important;
  font-weight: 100 !important;
  -webkit-text-stroke: 0px;
}
.welcome-items {
  margin-top: 15rem;
  display: flex;
  align-items: center;
  width: 100%;
  flex-direction: column;
}
.welcome-item {
  width: 100%;
  flex: 1;
  margin: 0.5rem;
  display: flex;
  align-items: center;
}
.welcome-item:nth-child(2) {
  flex-direction: row-reverse;
}
.welcome-item:nth-child(2) div {
  margin-left: 0;
  padding-left: 0;
}
.welcome-item h2 {
  font-weight: 1000;
  font-size: 5.5rem;
  color: #ffecb3;
  letter-spacing: 5px;
  text-align: start;
  margin-block-start: 0em;
}
.welcome-item p1 {
  font-size: 1.5rem;
  color: #ffecb3;
}
.welcome-item img {
  width: 40%;
  object-fit: cover;
  opacity: 0.7;
  box-shadow: 0px 0px 30px 0px black;
}
.welcome-item-text {
  padding: 20rem;
}
.welcome-item-text button {
  padding: 1.5rem 2rem;
  margin-top: 7rem;
  background: transparent;
  font-weight: 1000;
  font-size: 1.5rem;
  border: 0.5px solid tomato;
  box-shadow: 0px 0px 10px 0px tomato;
  border-radius: 5px;
  color: tomato;
}
